* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100%;
}

.flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

#root {
  height: 100vh;
}

#root .app {
  background: lightgray;
  padding: 1em;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-shadow: 1px 1px 2px lightgray;
          box-shadow: 1px 1px 2px lightgray;
}

#root .app .pads {
  width: 320px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

#root .app .pads .drum-pad {
  background: gray;
  width: 100px;
  height: 100px;
  margin: 2px;
  font-size: 1.6rem;
  font-weight: 600;
  -webkit-box-shadow: 1px 1px 2px gray;
          box-shadow: 1px 1px 2px gray;
  -webkit-transform: all 200ms ease;
          transform: all 200ms ease;
}

#root .app .pads .drum-pad:hover {
  opacity: 0.9;
}

#root .app .pads .drum-pad.active {
  -webkit-animation: slide 0.1s ease 0s 1;
          animation: slide 0.1s ease 0s 1;
  /* animation: slide 2s ease 0s infinite forwards; */
}

@-webkit-keyframes slide {
  0% {
    background: gray;
  }
  100% {
    background: orange;
    color: white;
  }
}

@keyframes slide {
  0% {
    background: gray;
  }
  100% {
    background: orange;
    color: white;
  }
}

#root .app .dashboard {
  padding: 2em;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

#root .app .dashboard .power__wrapper {
  width: 50px;
  height: 20px;
  background: white;
  padding: 2px;
  -webkit-box-shadow: 1px 1px 2px white;
          box-shadow: 1px 1px 2px white;
}

#root .app .dashboard .power__wrapper .power__button {
  display: block;
  width: 30px;
  height: 100%;
  background: blue;
  color: white;
  font-size: 0.7rem;
  -webkit-transform: translateX(15px);
          transform: translateX(15px);
  -webkit-transition: all 200ms ease-in-out;
  transition: all 200ms ease-in-out;
}

#root .app .dashboard .power__wrapper .power__button.slide__button {
  background: red;
  color: black;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

#root .app .dashboard .display__wrapper {
  border: none;
  width: 120px;
  height: 30px;
  background: orange;
  color: white;
  margin: 2em 0;
  font-size: 1.1rem;
  font-weight: 600;
  -webkit-box-shadow: 1px 1px 2px orange;
          box-shadow: 1px 1px 2px orange;
}
