#root {
  height: 100vh;

  .app {
    background: lightgray;
    padding: 1em;
    display: flex;
    box-shadow: 1px 1px 2px lightgray;

    .pads {
      width: 320px;
      display: flex;
      flex-wrap: wrap;

      .drum-pad {
        background: gray;
        width: 100px;
        height: 100px;
        margin: 2px;
        font-size: 1.6rem;
        font-weight: 600;
        box-shadow: 1px 1px 2px gray;
        transform: all 200ms ease;

        &:hover {
          opacity: 0.9;
        }

        &.active {
          animation: slide 0.1s ease 0s 1;
          /* animation: slide 2s ease 0s infinite forwards; */
        }

        @keyframes slide {
          0% {
            background: gray;
          }
          100% {
            background: orange;
            color: white;
          }
        }
      }
    }

    .dashboard {
      padding: 2em;
      flex-direction: column;

      .power__wrapper {
        width: 50px;
        height: 20px;
        background: white;
        padding: 2px;
        box-shadow: 1px 1px 2px white;

        .power__button {
          display: block;
          width: 30px;
          height: 100%;
          background: blue;
          color: white;
          font-size: 0.7rem;
          transform: translateX(15px);
          transition: all 200ms ease-in-out;

          &.slide__button {
            background: red;
            color: black;
            transform: translateX(0);
          }
        }
      }

      .display__wrapper {
        border: none;
        width: 120px;
        height: 30px;
        background: orange;
        color: white;
        margin: 2em 0;
        font-size: 1.1rem;
        font-weight: 600;
        box-shadow: 1px 1px 2px orange;
      }
    }
  }
}
